html{
    scroll-behavior: smooth;
}

.background {
    background-image: url(./background.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.mobile-margin {
    position: relative;
}

.header-title {
    padding-top: 2vh;
    opacity: 0.7;
}

.header-one {
    font-size: 7vh !important;
}

.menu-tag {
    display: flex;
    justify-content: flex-end;
    padding-top: 1vh;
    padding-right: 1vh;
}

.menu-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 7vh;
}

.card-wide {
    width: 80vw !important;
    opacity: 0.8 !important;
}

.google-map {
    width: 80vw;
    height: 30vh;
    border: 0;
}

.access-content {
    margin-bottom: 1vh;
}

.access-content p {
    font-size: 2vh;

}

.sns-button {
    width: 80vw;
    margin-bottom: 5vh;
    text-align: center;
}